import React, { Component } from 'react';
import {Tabs, Tab, Grid, Cell, Card, CardTitle, CardText, CardActions, CardMenu, Button, IconButton } from 'react-mdl';

class Projects extends Component{
    constructor(props) {
        super(props);

        this.state = { activeTab : 0 };
    }

    toggleCategories(){
        if (this.state.activeTab === 0) {
            return(
                <div className="projects-grid">
                    {/* Skill Track */}
                    <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                        <CardTitle style={{color: '#fff', height: '176px',
                        background: 'url(https://s3.amazonaws.com/coursetro/posts/174-full.png) center / cover'}}>Skill Tracker</CardTitle>
                        <CardText>
                            Lorem ipsum dolor sit amet, consectetur adipiscing elit.
                            Mauris sagittis pellentesque lacus eleifend lacinia...
                        </CardText>
                        <CardActions border>
                            <Button colored>GitHub</Button>
                            <Button colored>CodePEn</Button>
                            <Button colored>LiveDemo</Button>
                        </CardActions>
                        <CardMenu style={{color: '#fff'}}>
                            <IconButton name="share" />
                        </CardMenu>
                    </Card>

                    {/* Network Diagnosis */}
                    <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                        <CardTitle style={{color: '#fff', height: '176px',
                        background: 'url(https://s3.amazonaws.com/coursetro/posts/174-full.png) center / cover'}}>Network Diagnosis</CardTitle>
                        <CardText>
                            Lorem ipsum dolor sit amet, consectetur adipiscing elit.
                            Mauris sagittis pellentesque lacus eleifend lacinia...
                        </CardText>
                        <CardActions border>
                            <Button colored>GitHub</Button>
                            <Button colored>CodePEn</Button>
                            <Button colored>LiveDemo</Button>
                        </CardActions>
                        <CardMenu style={{color: '#fff'}}>
                            <IconButton name="share" />
                        </CardMenu>
                    </Card>

                    {/* Report Anaysis */}
                    <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                        <CardTitle style={{color: '#fff', height: '176px',
                        background: 'url(https://s3.amazonaws.com/coursetro/posts/174-full.png) center / cover'}}>Report Analysis</CardTitle>
                        <CardText>
                            Lorem ipsum dolor sit amet, consectetur adipiscing elit.
                            Mauris sagittis pellentesque lacus eleifend lacinia...
                        </CardText>
                        <CardActions border>
                            <Button colored>GitHub</Button>
                            <Button colored>CodePEn</Button>
                            <Button colored>LiveDemo</Button>
                        </CardActions>
                        <CardMenu style={{color: '#fff'}}>
                            <IconButton name="share" />
                        </CardMenu>
                    </Card>
                </div>
                
            )
        }else if(this.state.activeTab === 1){
            return(
                <div><h1>This is Python</h1>
                </div>
            )
        }else if(this.state.activeTab === 2){
            return(
                <div><h1>This is Node</h1>
                </div>
            )
        }else if(this.state.activeTab === 3){
            return(
                <div><h1>This is Java</h1>
                </div>
            )
        }else{
            return(
                <div><h1>This is React</h1>
                </div>
            )
        }
    }

    
    render(){
        return(
            <div className="category-tabs ">
                <Tabs activeTab={this.state.activeTab} onChange={(tabId) => this.setState({activeTab: tabId})}
                ripple>
                    <Tab>Angular JS</Tab>
                    <Tab>Python</Tab>
                    <Tab>Node JS</Tab>
                    <Tab>Java(EE)</Tab>
                    <Tab>React JS</Tab>
                </Tabs>

                {/* <section className="projects-grid"> */}
                    <Grid>
                        <Cell col={12}>
                            <div className="content">
                            {this.toggleCategories()}
                            </div>
                        </Cell>
                    </Grid>
                    
                {/* </section> */}
            </div>
        )
    }
}

export default Projects;